import React, { PureComponent } from 'react';
import './index.less'

export default class Index extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      resetCount: (props.limitTime / 1000) || 0, // 
    }
    this.timer = null; // 
    this.isInterrupt = false; // 
    if (props.limitTime > 0) { 
      this.countDown();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.limitTime !== nextProps.limitTime) {
      let time = (nextProps.limitTime >= 0) ? nextProps.limitTime : 0;
      time = time / 1000; // 
      this.setState({ resetCount: time }, () => {
        if (time) {
          this.clearTimer();
          this.countDown();
        }
      })
    }
  }

  componentWillUnmount() {
    this.clearTimer();
  }

  // 
  interrupt = () => { 
    this.isInterrupt = true;
  }

  // 
  clearTimer() {
    clearTimeout(this.timer);
    this.timer = null;
  }

  // 
  countDown = () => {
    if (this.isInterrupt) { // 
      this.clearTimer()
      return;
    }

    this.timer = setTimeout(() => {
      let { resetCount } = this.state;
      if (resetCount > 0) {
        resetCount--;
        this.setState({ resetCount }, () => {
          this.countDown();
        });
      } else {
        this.props.autoInterruptTime()
        this.clearTimer()
      }
    }, 1000)
  }

  //0-90101
 checkTime(i) {  
    if(i < 10) { 
      i = "0" + i; 
    } 
    return i; 
  } 

  /**
   * 
   * @param s 
   * @returns {*} 
   */
  parseTime(s) {
    if (s <= 0 || !s) return '00:00:00';
    let hours = parseInt(s / 60 / 60 % 24 , 10); // 
    let minutes = parseInt(s / 60 % 60, 10);// 
    let seconds = parseInt(s % 60, 10);// 
  hours = this.checkTime(hours); 
  minutes = this.checkTime(minutes); 
  seconds = this.checkTime(seconds); 

    return `${hours}:${minutes}:${seconds}`;
  }

  render() {
    const { isRestartTask, limitTime, interruptTime } = this.props;
    const { resetCount } = this.state;
    const time = this.parseTime(resetCount);
    const giveupTask = limitTime > 0;

    return (
      <div className="count-down flex items-center">
        <span className="time">{time}</span>
        { isRestartTask && <span></span> }
        { giveupTask && <button onClick={interruptTime} className="operate"></button> }
      </div>
    )
  }
}


